import React, { useState } from 'react';
import {ReactComponent as MyConetLogo} from '../assets/my-conet.logo.svg';
import {ReactComponent as FoxxuLogo} from '../assets/foxxu-logo.svg';

const HeroContent = {
    firstLine: ["Personal", "Kompetenz"],
    operanten: ["+", "–", "/", "%", "*"],
    firstlineSecond: "entwicklung",
    secondLine: "Mainfranken",
    text: "Ihr regionaler Kompetenzpartner für Personalentwicklung"
}



export default function LandingPage(){

    const [firstLineCount, setFirstLineCount] = useState(0);
    const [operantenCount, setOperantenCount] = useState(0);

    function handleAnimation(){
        
        if(operantenCount < HeroContent.operanten.length - 1){
            const newCount = operantenCount + 1;

            setOperantenCount(newCount);
        }
        
        else{
            const newFirstLine = 0;
            const newOperant = 0;

            setFirstLineCount(newFirstLine);
            setOperantenCount(newOperant);
        }
        
        
    }

 return(
    <section className="hero">
        <div class="container-fluid hero__container"> 
            <div className="offset-lg-1 hero__headline">
                <h2 className="hero__headline__container">
                    <span display="flex justify-content-between hero__headline__line">
                    <span className="hero__item"><span className="hero__animation">Personalentwicklung</span></span>
                    <span className="hero__item mt-2 mt-md-0 ml-md-4"><span className="hero__shuffleAnimation" onAnimationIteration={handleAnimation}>{HeroContent.operanten[operantenCount]}</span></span>
                    </span>
                    <span className="hero__item hero__item__second-line"><span>Mainfranken</span></span></h2>
            </div>
        </div>
        <div className="container-fluid claim">
            <div className="row">
                <div className="col-11 offset-md-1 claim__text">
                    <span>{HeroContent.text}</span>
                </div>
            </div>
        </div>

        <div className="hero__marken">
            <MyConetLogo className="hero__marke"/>
            <FoxxuLogo className="hero__marke"/>
        </div>


        


        <div className="container-fluid desktop__claim__marken">
            <div className="row d-flex">
                <div className="col-lg-4 offset-lg-1">
                    <span>{HeroContent.text} </span>
                </div>
                <div className="col-lg-2 offset-lg-2 d-flex justify-content-between">
                    <MyConetLogo className="hero__marke__desktop"/>
                    <FoxxuLogo className="hero__marke__desktop"/>
                </div>
            </div>
        </div>
    </section>
 );
}